  .ScrollBox__box {
    z-index: 1;
    padding: 0;
    transition: right 0.2s; }
  .ScrollBox__slider {
    opacity: 0;
    transition: opacity 0.4s; }
  .ScrollBox .VerticalSlider {
    position: absolute;
    z-index: 3;
    top: 5px;
    right: 2px;
    width: 10px; }
  .ScrollBox .VerticalSlider__bar {
    width: 10px;
    background: rgba(77, 92, 116, 0.1);
    border-radius: 5px;
    transition: background 0.2s; }
  .ScrollBox .VerticalSlider__cursor {
    position: absolute;
    width: 10px;
    background: rgba(77, 92, 116, 0.3);
    border-radius: 5px;
    transition: background 0.2s; }
  .ScrollBox .VerticalSlider:hover .VerticalSlider__cursor {
    background: rgba(77, 92, 116, 0.6); }
  .ScrollBox .VerticalSlider:hover .VerticalSlider__cursor:hover {
    background: #4d5c74; }
  .ScrollBox .VerticalSlider:hover .VerticalSlider__bar {
    background: rgba(77, 92, 116, 0.3); }
