.Icon{
  position:relative;
  & svg{
    position:absolute;
    display:block;
    top:50%;
    left:50%;
    transform:translateX(-50%) translateY(-50%);
  }


  & path,
  & circle{
    fill:white;
    transition: fill 0.2s;
  }
  &:hover{
    & path,
    & circle{
      fill:black;
    }
  }
}