.Ripple {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  opacity: 0.8;
  position: absolute;
  animation: Ripple__animation 0.5s;
  transform: scale(0);
}
@keyframes Ripple__animation {
  to {
    transform: scale(1.5);
    opacity: 0;
  }
}